.navbar {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  padding: 0.25em;
  background-color: white;
  z-index: 4;
}
.navbar #breadcrumbs {
  margin-left: 1em;
}
.navbar__breadcrumb {
  font-size: 1.75em;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  margin-bottom: 0.5em;
  text-decoration: none;
  display: inline-block;
  position: relative;
  color: #A91E24;
  text-decoration: none;
  cursor: pointer;
}
.navbar__breadcrumb--divider {
  color: #A91E24;
  margin: 0 0.1em;
}
.navbar__breadcrumb--divider svg {
  height: 15px;
  width: 15px;
}
@media screen and (max-width: 450px) {
  .navbar__breadcrumb--divider svg {
    height: 10px;
  }
}
@media screen and (max-width: 370px) {
  .navbar__breadcrumb--divider {
    display: none;
  }
}
@media screen and (max-width: 450px) {
  .navbar__breadcrumb {
    font-size: 1em;
  }
}
.navbar__breadcrumb::after {
  content: " ";
  background-color: #A91E24;
  transition: width 0.15s ease-out;
  width: 0px;
  height: 2px;
  margin: 0 auto;
  display: inline-block;
  position: absolute;
  top: 100%;
  left: 0;
}
.navbar__breadcrumb:hover::after {
  width: 100%;
}
.navbar__breadcrumb:visited {
  color: #A91E24;
}
@media screen and (max-width: 370px) {
  .navbar__breadcrumb {
    display: none;
  }
}

.navbarBackdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  backdrop-filter: blur(8px);
  background-color: rgba(0, 0, 0, 0.15);
  z-index: 3;
  transition: clip-path 0.15s ease-out;
  clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
}
.navbarBackdrop--expanded {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}

.expandableNavbar {
  position: fixed;
  z-index: 5;
  top: 0;
  right: 0;
  background-color: #A91E24;
  height: 100%;
  transition: clip-path 0.3s ease-out;
  width: 295px;
  clip-path: circle(100px at 100% 0);
}
.expandableNavbar__links {
  display: none;
}
.expandableNavbar__toggleArea {
  display: flex;
  margin-top: 0.25em;
  justify-content: flex-end;
}
.expandableNavbar--expanded {
  clip-path: circle(2000px at 100% 0);
}
.expandableNavbar--expanded .expandableNavbar__links {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 0 2.5em;
}
.expandableNavbar--expanded .expandableNavbar__links a {
  font-size: 30px;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  margin-bottom: 0.5em;
  text-decoration: none;
  display: inline-block;
  position: relative;
  color: white;
}
.expandableNavbar--expanded .expandableNavbar__links a::after {
  content: " ";
  background-color: white;
  transition: width 0.15s ease-out;
  width: 0px;
  height: 2px;
  margin: 0 auto;
  display: inline-block;
  position: absolute;
  top: 100%;
  left: 0;
}
.expandableNavbar--expanded .expandableNavbar__links a:hover::after {
  width: 100%;
}
.expandableNavbar--expanded .expandableNavbar__links a:visited {
  color: white;
}
.expandableNavbar button {
  background-color: #A91E24;
  cursor: pointer;
  transition: background-color 0.3s ease-out;
  border: none;
  padding: 0.25em 0.5em;
  border-radius: 5px;
}
.expandableNavbar button svg {
  height: 40px;
  width: 30px;
}
.expandableNavbar button:hover {
  background-color: #7e161b;
}